/* styles.css */
*{box-sizing:border-box}
:root{
  --bg:#0b1220; --text:#e5e7eb; --muted:#9ca3af; --card:#0f172a;
  --primary:#0ea5e9; --primary2:#38bdf8; --shadow:0 10px 30px rgba(0,0,0,.25);
}
:root:not(.dark){
  --bg:#f6f8fb; --text:#0f172a; --muted:#475569; --card:#ffffff;
  --primary:#0ea5e9; --primary2:#38bdf8;
}
html,body{margin:0;background:var(--bg);color:var(--text);font-family:Inter,system-ui,Segoe UI,Roboto,Arial,sans-serif}
a{color:var(--primary);text-decoration:none}
.nav{position:sticky;top:0;z-index:50;display:flex;align-items:center;justify-content:space-between;gap:16px;padding:10px 16px;background:linear-gradient(90deg,rgba(14,165,233,.08),rgba(56,189,248,.06));backdrop-filter:blur(8px)}
.brand{font-weight:800}
.links{display:flex;gap:14px;flex-wrap:wrap}
.links a{font-weight:600;opacity:.95}
.toggle{border:0;background:var(--card);color:var(--text);padding:8px 10px;border-radius:8px;box-shadow:var(--shadow);cursor:pointer}
.hero{position:relative;overflow:hidden}
.particles{position:absolute;inset:0;width:100%;height:100%;display:block}
.heroContent{position:relative;z-index:1;display:grid;place-items:center;padding:72px 16px 64px;text-align:center}
.title{font-size:clamp(28px,8vw,56px);margin:0 0 8px}
.subtitle{opacity:.95;font-weight:600}
.cta{margin-top:16px;display:flex;gap:12px;justify-content:center;flex-wrap:wrap}
.btn{border:0;border-radius:12px;padding:12px 18px;background:#ffffff20;color:#fff;backdrop-filter:blur(6px);cursor:pointer;transition:.25s}
.btn:hover{transform:translateY(-2px)}
.btn.primary{background:linear-gradient(90deg,var(--primary),var(--primary2));color:#0b1220;font-weight:800}

h2{text-align:center;margin:0 0 24px;font-size:clamp(22px,4vw,32px)}
.lead{max-width:900px;margin:0 auto 16px;color:var(--muted)}
.meta{display:flex;gap:14px;justify-content:center;flex-wrap:wrap;color:var(--muted)}
.avatar{width:200px;height:200px;border-radius:50%;object-fit:cover;display:block;margin:16px auto;box-shadow:var(--shadow)}
.grid.skills{max-width:1100px;margin:0 auto;display:grid;grid-template-columns:repeat(auto-fit,minmax(220px,1fr));gap:16px}
.card{background:var(--card);border-radius:16px;box-shadow:var(--shadow);padding:18px}
.row{display:flex;justify-content:space-between;align-items:center}
.bar{height:10px;background:#1f2937;border-radius:999px;overflow:hidden;margin-top:10px}
:root:not(.dark) .bar{background:#e5e7eb}
.bar i{display:block;height:100%;width:0;background:linear-gradient(90deg,var(--primary),var(--primary2));border-radius:999px;transition:width 1.2s}
.timeline{max-width:900px;margin:0 auto;padding:0;list-style:none}
.timeline .card{margin:14px 0;border-left:4px solid var(--primary)}
.date{font-weight:800;color:var(--primary);display:block;margin-bottom:6px}
.carousel{max-width:1100px;margin:0 auto;overflow:hidden;border-radius:16px}
.track{display:flex;transition:transform .6s}
.slide{min-width:100%;padding:6px}
.flip .front,.flip .back{min-height:160px;border-radius:16px;padding:18px;backface-visibility:hidden;transition:transform .6s;background:var(--card);box-shadow:var(--shadow)}
.flip .back{transform:rotateY(180deg);display:flex;align-items:center;justify-content:center;gap:10px}
.flip:hover .front{transform:rotateY(180deg)}
.flip:hover .back{transform:rotateY(360deg)}
.contactRow{display:flex;gap:12px;justify-content:center;margin-bottom:12px;flex-wrap:wrap}
.contactForm{max-width:720px;margin:0 auto;display:grid;gap:12px}
.contactForm input,.contactForm textarea{background:var(--card);color:var(--text);border:1px solid transparent;border-radius:10px;padding:12px;box-shadow:var(--shadow)}
.contactForm input:focus,.contactForm textarea:focus{outline:none;border-color:var(--primary)}
.footer{text-align:center;color:var(--muted);padding:24px}
.progress{position:fixed;inset:0 auto auto 0;height:6px;background:linear-gradient(90deg,var(--primary),var(--primary2));transform-origin:left top;transform:scaleX(0);z-index:60;border-radius:0 8px 8px 0}
.toTop{position:fixed;right:16px;bottom:16px;border:0;border-radius:50%;width:46px;height:46px;background:linear-gradient(180deg,var(--primary),var(--primary2));color:#0b1220;font-weight:800;box-shadow:var(--shadow);cursor:pointer;opacity:.9}
.rainbow{animation:rainbow 12s linear infinite}
@keyframes rainbow{to{filter:hue-rotate(360deg)}}
.type{overflow:hidden;border-right:.12em solid rgba(255,255,255,.8);white-space:nowrap;margin:0 auto;letter-spacing:.02em;animation:typing 4s steps(40,end), blink .9s step-end infinite;max-width:95vw}
@keyframes typing{from{width:0}to{width:100%}}@keyframes blink{50%{border-color:transparent}}

.tilt{transform-style:preserve-3d;transition:transform .2s}
.tilt:hover{transform:perspective(600px) rotateX(4deg) rotateY(-3deg) translateY(-3px)}

